import { NgModule ,NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { ItemMasterRoutingModule } from './item-master-routing.module';
import { ItemMasterComponent } from './item-master.component';
import { ItemMasterAddComponent } from './item-master-add/item-master-add.component';
import { ItemMasterEditComponent } from './item-master-edit/item-master-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ItemMasterRoutingModule
  ],
  entryComponents:[
    ItemMasterAddComponent,
    ItemMasterEditComponent
  ],
  declarations: [
    ItemMasterComponent,
    ItemMasterAddComponent,
    ItemMasterEditComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class ItemMasterModule { }



