import { Component, OnInit, Input, Output, EventEmitter, ViewChild, Inject, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ItemMasterService, Item,ProductCategoryMasterService, CurrentUserService,ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxTextAreaComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtextarea';
import { jqxCheckBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcheckbox';
import { jqxTreeComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtree';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-item-master-edit',
  templateUrl: './item-master-edit.component.html',
  styleUrls: ['./item-master-edit.component.scss']
})
export class ItemMasterEditComponent implements OnInit {
  @ViewChild('mydes') mydes: jqxTextAreaComponent;
  @ViewChild('PcComboBox') PcComboBox: jqxComboBoxComponent;
  @ViewChild('uComboBox') uComboBox: jqxComboBoxComponent;
  @ViewChild('myComboBox2') myComboBox2: jqxComboBoxComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('eiCheckBox') eiCheckBox: jqxCheckBoxComponent; 
  @ViewChild('myTree') myTree: jqxTreeComponent;
  @Input('ItemEditData') ItemEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  EditData: Item;
  itemForm: FormGroup;
  productCategory_data: any;
  productCategoryAdapter: any;
  unit_data: any;
  unitAdapter: any;
  estate_data: any;
  estateAdapter: any;
  apiUrl: string;

  treeSource: any;
  treeDataAdapter: any;
  treeRecords: any = [];
  treeData: any;

  userData: any = {};

  constructor(private fb: FormBuilder,
    private cdr: ChangeDetectorRef,
    @Inject('API_URL') apiUrl: string,
    private ims: ItemMasterService,
    private pcmService: ProductCategoryMasterService,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }

  ngOnInit() {
    this.initTreeData();
    this.productCategory_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'product_category?limit=all',
      };
    this.productCategoryAdapter = new jqx.dataAdapter(this.productCategory_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.PcComboBox.selectItem(this.ItemEditData.product_category_id);
      }
    });
    this.unit_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'unit?limit=all',
      };
    this.unitAdapter = new jqx.dataAdapter(this.unit_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.uComboBox.selectItem(this.ItemEditData.unit_id);
      }
    });
    this.estate_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'tea_estate?limit=all',
      };
    this.estateAdapter = new jqx.dataAdapter(this.estate_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.myComboBox2.selectItem(this.ItemEditData.estate_id);
      }
    });
    this.EditData = new Item(this.ItemEditData);
    this.itemForm.setValue(this.EditData);
  }
  ngAfterViewInit() {
    console.log(this.EditData.description);
    this.mydes.val(this.EditData.description);
    if(this.ItemEditData.essential_item==1){
      this.eiCheckBox.check();
    }else{
      this.eiCheckBox.uncheck();
      
    }
    this.cdr.detectChanges();
  }
 
 
  createForm() {
    this.itemForm = this.fb.group({
      'code': [null, Validators.required],
      'name': [null, Validators.required],
      'description': [null, Validators.required],
      'minimum_stock': [null, Validators.required],
      'maximum_stock': [null, Validators.required],
      'reorder_quantity': [null, Validators.required],

    });
  }
  initTreeData() {
    let post = [];
    post['limit'] = 'all';
    this.pcmService.index(post).subscribe(
      result => {
        this.treeData = result;
        this.treeSource = {
          datatype: 'json',
          datafields: [
            { name: 'id' },
            { name: 'parent_id' },
            { name: 'code' },
            { name: 'name' },
            { name: 'children' }
          ],
          id: 'id',
          localdata: this.treeData
        };
        // create data adapter & perform Data Binding.
        this.treeDataAdapter = new jqx.dataAdapter(this.treeSource, {
          autoBind: true,
          loadComplete: () => {
          }

        });
        this.treeRecords = this.treeDataAdapter.getRecordsHierarchy('id', 'parent_id', 'items', [{ name: 'name', map: 'label' }]);
        setTimeout(() => {
          this.myTree.expandAll();
          let items = this.myTree.getItems();
          for (let i = 0; i < items.length; i++) {
            let currentItem = items[i];
            if (currentItem['id'] == this.ItemEditData.product_category_id) {
              this.myTree.checkItem(currentItem.element, true);
            };
          };
        }, 2000);
      },
      error => {
        console.log(error);
      }
    )
  }

  TreeCheckChange(e) {
    if (e.args.checked) {
      let selectedData;
      selectedData = this.myTree.getCheckedItems();
      if (selectedData.length > 1) {
        alert('Select Only One');
        this.myTree.uncheckAll();
      } else if (selectedData.length < 1) {
        alert('Atleast Select One');
      } else {

      }
    }
  }
  saveBtn(post) {
    let selectedData;
    selectedData = this.myTree.getCheckedItems();
    if (selectedData.length > 0) {
      post['product_category_id'] = selectedData[0].id;
    } else {
      post['product_category_id'] = null;
    }
    post['unit_id'] = this.uComboBox.val();
     post['essential_item'] = this.eiCheckBox.val();
    this.myValidator.validate(document.getElementById('itemForm'));
    if (post['product_category_id'] && post['unit_id']) {
      this.jqxLoader.open();
      this.ims.update(this.ItemEditData.id, post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        });
    }
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.codeInput', message: 'Code is required!', action: 'keyup, blur', rule: 'required' },
      { input: '.nameInput', message: ' Name is required!', action: 'keyup, blur', rule: 'required' },
      { input: '.descriptionInput', message: ' Description is required!', action: 'keyup, blur', rule: 'required' },
      { input: '.reorder_quantityInput', message: ' This reorder quantity is required!', action: 'keyup, blur', rule: 'required' },
      // {
      //   input: '.product_category_idInput', message: ' This field is required!', action: 'change',
      //   rule: (input: any, commit: any): any => {

      //     let data = this.PcComboBox.val();
      //     let result = (data != '') ? true : false;
      //     return result;
      //   }
      // },
      {
        input: '.unit_idInput', message: ' Unit is required!', action: 'change', rule: (input: any, commit: any): any => {
          let data = this.uComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      { input: '.minimum_stockInput', message: ' Minimum stock is required!', action: 'keyup, blur', rule: 'required' },
      { input: '.maximum_stockInput', message: ' Maxium stock is required!', action: 'keyup, blur', rule: 'required' },
  //    { input: '.quantityInput', message: ' Quantity is required!', action: 'keyup, blur', rule: 'required' },
    ];
}



