import { Component, OnInit, ViewChild, Inject, ComponentFactoryResolver, ElementRef, ViewContainerRef } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { ItemMasterService,ProductCategoryMasterService, UnitMasterService, QuickaccessComponent, CurrentUserService } from '../../../shared';
import { ItemMasterAddComponent } from './item-master-add/item-master-add.component';
import { ItemMasterEditComponent } from './item-master-edit/item-master-edit.component';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxTreeGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtreegrid';
import { jqxCheckBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcheckbox';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';

@Component({
  selector: 'app-item-master',
  templateUrl: './item-master.component.html',
  styleUrls: ['./item-master.component.scss']
})
export class ItemMasterComponent implements OnInit {
  @ViewChild('groupTreeGrid') groupTreeGrid: jqxTreeGridComponent;
  @ViewChild('groupGrid') groupGrid: jqxGridComponent;
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('errNotification') errNotification: jqxNotificationComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('jtransactCheck') transactCheck: jqxCheckBoxComponent;
  @ViewChild('essentialItemCheck') essentialItemCheck: jqxCheckBoxComponent;
  @ViewChild('productCategoryCombo') productCategoryCombo: jqxComboBoxComponent;
  @ViewChild('uComboBox') uComboBox: jqxComboBoxComponent;
  @ViewChild('itemWindow') itemWindow: jqxWindowComponent;
  
  /**
     * Global Variable decleration
     */
    imForm: FormGroup;
    treeSource: any;
    treeDataAdapter: any;
    treeColumns: any = [];
    gridSource: any;
    gridDataAdapter: any;
    gridColumns: any = [];
    trasValue: any;
    selectedTreeGroupId: string;
  
    deleteRowIndexes: Array<any> = [];
  
    isChecked: boolean = false;
    selectedItem: string = '';

    productCategoryAdapter:any = [];
    unitAdapter: any = [];
  
    constructor(
      private fb: FormBuilder,
      private ims: ItemMasterService,
      private pcm: ProductCategoryMasterService,
      private ums: UnitMasterService,
    ) {
      this.createForm();
    }
  
    /**
     * Create the form group 
     * with given form control name 
     */
    createForm() {
      this.imForm = this.fb.group({
        'name': [null, Validators.required],
        'code': [null, Validators.required],
        'description': [null],
        'minimum_stock':[null],
        'maximum_stock':[null],
        'reorder_quantity':[null],
        'essential_item':[null],
      });
    }
  
    ngOnInit() {
      let post = [];
      post['limit'] = 'all';
      this.pcm.index(post).subscribe(
        result => {
          this.productCategoryAdapter = result;
        },
        error => {
          console.log(error);
        }
      );

      this.ums.index({limit:'all'}).subscribe(
        result => {
          this.unitAdapter = result;
        },
        
        error => {
          console.log(error);
        }
      )

      localStorage.removeItem('selectedTreeItem');
      this.treeSource =
        {
          dataType: "json",
          dataFields: [
            {name:'id', type:'string'},
            { name: 'code', type: 'string' },
            { name: 'name', type: 'string' },
            { name: 'parent_id', type:'number'},
            { name: 'product_category_name', type: 'string', map:'product_category>name' }

          ],
          hierarchy:
            {
              keyDataField: { name: 'id' },
              parentDataField: { name: 'parent_id' }
            },
          id: 'id',
          localdata: []
        };
  
      this.treeDataAdapter = new jqx.dataAdapter(this.treeSource);
  
      this.treeColumns =
        [
          { text: 'Code', dataField: 'code', width: '130', filterable:true },
          { text: 'Name', dataField: 'name', width: '200', filterable:true },
          { text: 'Product Type', dataField: 'product_category_name' }
        ];
  
  
      this.gridSource =
        {
          datatype: 'json',
          datafields: [
            { name:'id', type:'string'},
            { name: 'code', type: 'string' },
            { name: 'name', type: 'string' },
            { name: 'parent_id', type:'number'},
            { name: 'product_category_name', type: 'string', map:'product_category>name' },
            { name: 'transact', type: 'string' },
            { name: 'essential_item'},
            { name: 'maximum_stock'},
            { name: 'minimum_stock'},
            { name: 'reorder_quantity'},
            { name: 'unit'}
          ],
          id: 'id',
          pagesize: 20,
          localdata: [],
        };
  
      this.gridDataAdapter = new jqx.dataAdapter(this.gridSource);
  
      this.gridColumns =
        [
          {
            text: 'S.N', sortable: false, filterable: false, editable: false,
            groupable: false, draggable: false, resizable: false,
            datafield: '', columntype: 'number', width: 50,
            cellsrenderer: function (row, column, value) {
              return "<div style='margin:4px;'>" + (value + 1) + "</div>";
            }
          },
          { text: 'Code', dataField: 'code', width: '130' },
          { text: 'Name', dataField: 'name', width: '200' },
          { text: 'Transaction', datafield: 'transact' },
        ];
  
    }
  
  
    ngAfterViewInit() {
      this.loadTreeData();
    }
  
    ngOnDestroy() {
      localStorage.removeItem('selectedTreeItem');
    }
  
    Change($e) {
      this.isChecked = $e.args.checked;
      if (!this.isChecked) {
        this.imForm.get('minimum_stock').reset();
        this.imForm.get('maximum_stock').reset();
        this.imForm.get('reorder_quantity').reset();
        this.imForm.get('essential_item').reset();

        this.imForm.get('minimum_stock').clearValidators();
        this.imForm.get('minimum_stock').updateValueAndValidity();
        this.imForm.get('maximum_stock').clearValidators();
        this.imForm.get('maximum_stock').updateValueAndValidity();
        // this.imForm.get('essential_item').clearValidators();
        // this.imForm.get('essential_item').updateValueAndValidity();
        // this.imForm.get('assetCode').enable();
        // this.imForm.get('assetCode').setValidators(Validators.required);
      } else {
        
        // this.imForm.get('assetCode').disable();
        this.imForm.get('minimum_stock').setValidators(Validators.required);
        this.imForm.get('minimum_stock').updateValueAndValidity();
        this.imForm.get('maximum_stock').setValidators(Validators.required);
        this.imForm.get('maximum_stock').updateValueAndValidity();
        // this.imForm.get('essential_item').setValidators(Validators.required);
        // this.imForm.get('essential_item').updateValueAndValidity();
      }
    }
  
    /**
     * Row selected event in Tree Grid
     * @param $event
     */
    treeRowSelect($event) {
      // console.log($event);
      this.deleteRowIndexes = [];
      this.selectedTreeGroupId = $event.args['key'];
      this.treeRowSelectedData(this.selectedTreeGroupId);
      localStorage.setItem('selectedTreeItem', JSON.stringify($event.args.row));
      this.selectedItem = $event && $event.args && $event.args.row && $event.args.row['name'];
    }


    resetSelection(){
      localStorage.removeItem('selectedTreeItem');
      this.selectedItem = null;
      this.gridSource.localdata = [];
      this.groupGrid.updatebounddata();
    }
    
  
    /**
     * Get child Data of selected row in Tree Grid
     * and display the childern in Grid
     * @param groupId 
     */
    treeRowSelectedData(groupId) {
      this.groupGrid.clearselection();
      let post = {};
      post['parent_id'] = groupId;
      // post['transact'] = 1;
      post['limit'] = 'all';
      console.log(groupId);
      if (post['parent_id']) {
        this.ims.index(post).subscribe(
          response => {
            console.info(response);
            let TreeChild = response;
            if (response.length < 1) {
            //   let messageDiv: any = document.getElementById('error');
            //   messageDiv.innerText = response[0].error;
            //   this.errNotification.open();
              this.gridSource.localdata = [];
            } else {
              this.gridSource.localdata = response;
            }
            this.groupGrid.updatebounddata();
          },
          error => {
            this.gridSource.localdata = [];
            this.groupGrid.updatebounddata();
            console.log(error);
          }
        )
      }
    }
  
  
    /**
     * Grid row checked event
     * @param event 
     */
    rowChange(event: any) {
      this.deleteRowIndexes.push(event.args.rowindex);
    }
  
    /**
     * Grid row unchecked event
     * @param event 
     */
    rowUnChange(event: any) {
      let index = this.deleteRowIndexes.indexOf(event.args.rowindex);
      if (index > -1) {
        this.deleteRowIndexes.splice(index, 1);
      }
  
    }
  
    /**
     * Child Grid Toolbar
     */
    gridRenderToolbar = (toolbar: any): void => {
      let container = document.createElement('div');
      container.style.margin = '5px';
  
      let buttonContainer3 = document.createElement('div');
  
      buttonContainer3.id = 'buttonContainer3';
  
      buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
  
      container.appendChild(buttonContainer3);
      toolbar[0].appendChild(container);
  
      let deleteRowButton = jqwidgets.createInstance('#buttonContainer3', 'jqxButton', { width: 150, value: 'Delete Selected Row', theme: 'energyblue' });
  
      deleteRowButton.addEventHandler('click', () => {
        let id = this.deleteRowIndexes;
        let ids = [];
        let rowscount = this.groupGrid.getdatainformation().rowscount;
        for (let i = 0; i < id.length; i++) {
          let dataRecord = this.groupGrid.getrowdata(Number(id[i]));
          ids.push(dataRecord['group_code']);
          let testId = this.groupGrid.getrowid(Number(id[i]));
          this.groupGrid.deleterow(testId);
        }
        //Load Grid After Item Have been deleted.
        this.treeRowSelectedData(this.selectedTreeGroupId);
  
        if (ids.length > 0 && ids.length <= parseFloat(rowscount)) {
          if (confirm("Are you sure? You Want to delete")) {
            this.jqxLoader.open();
            this.ims.destroy('(' + ids + ')').subscribe(result => {
              this.jqxLoader.close();
              if (result['message']) {
                this.groupGrid.clearselection();
                this.deleteRowIndexes = [];
                let messageDiv: any = document.getElementById('message');
                messageDiv.innerText = result['message'];
                this.msgNotification.open();
              }
              if (result['error']) {
                this.groupGrid.clearselection();
                this.deleteRowIndexes = [];
                let messageDiv: any = document.getElementById('error');
                messageDiv.innerText = result['error']['message'];
                this.errNotification.open();
              }
  
              // Reload Tree grid after item deletion
              this.loadTreeData();
  
              //Reload grid after item deletion
              this.treeRowSelectedData(this.selectedTreeGroupId);
  
            }, (error) => {
              this.jqxLoader.close();
              console.info(error);
            });
          }
          this.groupGrid.updatebounddata();
        } else {
          let messageDiv = document.getElementById('error');
          messageDiv.innerText = 'Please select some item to delete';
          this.errNotification.open();
        }
      })
  
    }; //render toolbar ends
  
  
    /**
     * Load Tree Grid Data
     */
    loadTreeData() {
      this.ims.index({limit:'all',transact:0}).subscribe(
        res => {

            this.loadTreeSource(res);
          // if (res['data']['data'].length < 1) {
          //   let messageDiv: any = document.getElementById('error');
          //   messageDiv.innerText = res['data'][0].error;
          //   this.errNotification.open();
          //   this.loadTreeSource([]);
          // } else {
          //   this.loadTreeSource(res['data']['data']);
            
          // }
        },
        error => {
          console.log(error);
        }
      )
    }
  
    /**
     *  Load Tree Grid Source with Data
     * @param data 
     */
    loadTreeSource(data) {
      this.treeSource.localdata = data;
      this.groupTreeGrid.updateBoundData();
      let datat = localStorage.getItem('selectedTreeItem');
      this.groupTreeGrid.expandAll();
      if(datat){
        let selectedData = JSON.parse(datat);
        this.groupTreeGrid.selectRow(selectedData['code']);
      }
      
    }

    childData:any = null;
    itemRowclick($event){
      console.log($event);
      this.childData = $event && $event.args && $event.args.row && $event.args.row.bounddata || null;

      if(this.childData){
        this.itemWindow.open();
      }else{
        this.childData = null;
      }
      
    }
  
    /**
     * Function triggered when save button is clicked
     * @param formData 
     */
    save(formData) {
      let treeGridData = this.groupTreeGrid.getSelection();
      if (this.isChecked) {
        formData['transact'] = 1;
        formData['essential_item'] = (this.essentialItemCheck.val()) ? 1 : 0;
      } else {
        formData['transact'] = 0;
      }
      let treeData = {};
      let data = localStorage.getItem('selectedTreeItem');
      let selectedData:any;
      if (data) {
        selectedData = JSON.parse(data);
        formData['parent_id'] = selectedData['id'];
      }
      formData['product_category_id'] = this.productCategoryCombo.val() || null;
      console.log(this.uComboBox.val());
      formData['unit_id'] = this.uComboBox.val() || null;
      console.log(formData);
      if (formData && formData['product_category_id'] && formData['unit_id']) {
        this.jqxLoader.open();
        this.ims.store(formData).subscribe(
          result => {
            if (result['message']) {
              if(selectedData){
                let groupId  = selectedData['id'];
                this.treeRowSelectedData(groupId);
              }
              let messageDiv: any = document.getElementById('message');
              messageDiv.innerText = result['message'];
              this.msgNotification.open();
              this.loadTreeData();
              this.imForm.reset();
              // this.gridSource.localdata = [];
              // this.groupGrid.updatebounddata();
            }
            this.jqxLoader.close();
            if (result['error']) {
              let messageDiv: any = document.getElementById('error');
              messageDiv.innerText = result['error']['message'];
              this.errNotification.open();
            }
          },
          error => {
            this.jqxLoader.close();
            console.info(error);
          }
        );
      } else {
        let messageDiv: any = document.getElementById('error');
        messageDiv.innerText = 'Please Enter all required field.';
        this.errNotification.open();
      }
    }
}


