import { Component, OnInit, Output, EventEmitter, ViewChild, Inject } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ItemMasterService ,ProductCategoryMasterService, ErrorHandleService ,CurrentUserService} from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxCheckBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcheckbox';
import { jqxTreeComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtree';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-item-master-add',
  templateUrl: './item-master-add.component.html',
  styleUrls: ['./item-master-add.component.scss']
})
export class ItemMasterAddComponent implements OnInit {
  @ViewChild('PcComboBox') PcComboBox: jqxComboBoxComponent;
  @ViewChild('PscComboBox') PscComboBox: jqxComboBoxComponent;
  @ViewChild('uComboBox') uComboBox: jqxComboBoxComponent;
  @ViewChild('myComboBox2') myComboBox2: jqxComboBoxComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('eiCheckBox') eiCheckBox: jqxCheckBoxComponent;
  @ViewChild('myTree') myTree: jqxTreeComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  itemForm: FormGroup;
  code: string;
  name: string;
  description: string;
  product_category_id: number;
  product_sub_category_id: number;
  unit_id: number;
  minimum_stock: number;
  maximum_stock: number;
  qunatity: number;
  reorder_quantity: number;

  productCategory_data: any;
  productCategoryAdapter: any;
  productSubCategory_data: any;
  productSubCategoryAdapter: any;
  unit_data: any;
  unitAdapter: any;
  estate_data: any;
  estateAdapter: any;
  apiUrl: string;
  treeSource: any;
  treeDataAdapter: any;
  treeRecords: any = [];
  treeData: any;

  userData: any = {};

  constructor(
    private itemService: ItemMasterService,
    private pcmService: ProductCategoryMasterService,
    private ehs: ErrorHandleService,
    @Inject('API_URL') apiUrl: string,
    private fb: FormBuilder,
    private cus: CurrentUserService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.itemForm = fb.group({
      'code': [null, Validators.required],
      'name': [null, Validators.required],
      'description': [null, Validators.required],
      'reorder_quantity': [null, Validators.required],
      'minimum_stock': [null, Validators.required],
      'maximum_stock': [null, Validators.required],

    });
  }

  ngOnInit() {
    this.productCategory_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'product_category?limit=all',
      };
    this.productCategoryAdapter = new jqx.dataAdapter(this.productCategory_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.unit_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'unit?limit=all',
      };
    this.unitAdapter = new jqx.dataAdapter(this.unit_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.estate_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'tea_estate?limit=all',
      };
    this.estateAdapter = new jqx.dataAdapter(this.estate_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.initTreeData();

  }
  initTreeData() {
    let post = [];
    post['limit'] = 'all';
    this.pcmService.index(post).subscribe(
      result => {
        console.log(result);
        this.treeData = result;
        this.treeSource = {
          datatype: 'json',
          datafields: [
            { name: 'id' },
            { name: 'parent_id' },
            { name: 'code' },
            { name: 'name' },
          ],
          data: this.userData,
          id: 'id',
          localdata: this.treeData
        };
        // create data adapter & perform Data Binding.
        this.treeDataAdapter = new jqx.dataAdapter(this.treeSource, { autoBind: true });
        this.treeRecords = this.treeDataAdapter.getRecordsHierarchy('id', 'parent_id', 'items', [{ name: 'name', map: 'label' }]);
        // for(let i = 0; i < this.treeData.length; i++){
        //   let currentLoop  = this.treeData[i];
        //   if(currentLoop['children'] && currentLoop['children'].length > 0){
        //     console.log('inside loop if ' + currentLoop['id'] + ' ' + currentLoop['name']);
        //     this.myTree.disableItem(currentLoop['id']);
        //   }
        // }
        
      },
      error => {
        console.log(error);
      }
    )
  }
  TreeCheckChange(e){
    console.log(e);
    if (e.args.checked) {
      let selectedData;
      selectedData = this.myTree.getCheckedItems();
      if(selectedData.length > 1){
        alert('Select Only One');
        this.myTree.uncheckAll();
      }else if(selectedData.length < 1){
        alert('Atleast Select One');
      }else{
  
      }
    }

  }
  saveBtn(post) {
    let selectedData;
    selectedData = this.myTree.getCheckedItems();
    if(selectedData.length > 0){
      post['product_category_id'] = selectedData[0].id;
    }else{
      post['product_category_id'] = null;
    }
    post['unit_id'] = this.uComboBox.val();
    post['essential_item'] = this.eiCheckBox.val();
    this.myValidator.validate(document.getElementById('itemForm'));
    if (post['product_category_id'] && post['unit_id']) {
      this.itemService.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.codeInput', message: 'Code is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.nameInput', message: ' Name is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.descriptionInput', message: ' Description is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.reorder_quantityInput', message: ' This reorder quantity is required!', action: 'keydown, blur', rule: 'required' },
      // {
      //   input: '.product_category_idInput', message: ' This field is required!', action: 'keydown, blur',
      //   rule: (input: any, commit: any): any => {
      //     let data = this.PcComboBox.val();
      //     let result = (data != '') ? true : false;
      //     return result;
      //   }
      // },
      {
        input: '.unit_idInput', message: ' Unit is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.uComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      { input: '.minimum_stockInput', message: ' Minimum stock is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.maximum_stockInput', message: ' Maxium stock is required!', action: 'keydown, blur', rule: 'required' },
    //  { input: '.quantityInput', message: ' Quantity is required!', action: 'keydown, blur', rule: 'required' },
    ];
}



